import { SimpleButton2D } from "app/Display/SimpleButton2D";
import { TweenLite, TweenMax } from "gsap";
export class Grid extends PIXI.Container {
  private _symbol: SimpleButton2D[][] = [];
  private _rotateAnimation: TweenMax;
  private _scaleAnimation: TweenMax;
  constructor(private readonly sequence: string[][]) {
    super();
    this.init();
  }

  private init() {
    for (let i = 0; i < 9; i++) {
      this._symbol[i] = [];
      for (let j = 0; j < 9; j++) {
        this._symbol[i][j] = new SimpleButton2D(
          this.sequence[j][i],
          { x: 80 + i * 75, y: 360 + j * 90 },
          this.onButtonClick.bind(this, [i, j]),
          "solid1"
        );
        this._symbol[i][j].anchor.set(0.5, 0.5);
        this._symbol[i][j].scale.set(0.75, 0.75);
        this.addChild(this._symbol[i][j]);
      }
    }
  }

  private onButtonClick(index: number[], button: SimpleButton2D): void {
    const matches = this.findCluster();
    matches.forEach((match: any) => {
      console.log(match, index);
      if (match.column == index[1] && match.row == index[0]) {
        for (let i = 0; i < match.length; i++) {
          if (match.horizontal) {
            this._symbol[index[0]][index[1] + i].destroy();
          } else {
            this._symbol[index[0] + i][index[1]].destroy();
          }
        }
      } else {
        if (button) this.rotateAnimation(button);
        return;
      }
    });
  }

  private rotateAnimation(target: SimpleButton2D): void {
    if (this._rotateAnimation) this._rotateAnimation.seek(1, false);
    if (this._scaleAnimation) this._scaleAnimation.seek(1, false);
    TweenMax.killTweensOf(target.scale);
    TweenMax.killTweensOf(target.rotation);
    this._rotateAnimation = TweenMax.to(target, 0.1, {
      rotation: Math.random() < 0.5 ? 0.1 : -0.1,
      repeat: 5,
      yoyo: true,
    });
    this._scaleAnimation = TweenMax.to(target.scale, 0.1, {
      x: 0.65,
      y: 0.65,
      repeat: 3,
      yoyo: true,
    });
  }

  private findCluster(): any {
    // Reset clusters
    let clusters = [];
    // Find horizontal clusters
    for (var j = 0; j < 9; j++) {
      // Start with a single tile, cluster of 1
      var matchlength = 1;
      for (var i = 0; i < 9; i++) {
        var checkcluster = false;

        if (i == 9 - 1) {
          // Last tile
          checkcluster = true;
        } else {
          // Check the type of the next tile
          if (
            this.sequence[i][j] == this.sequence[i + 1][j] &&
            this.sequence[i][j] != ""
          ) {
            // Same type as the previous tile, increase matchlength
            matchlength += 1;
          } else {
            // Different type
            checkcluster = true;
          }
        }

        // Check if there was a cluster
        if (checkcluster) {
          if (matchlength >= 2) {
            // Found a horizontal cluster
            clusters.push({
              column: i + 1 - matchlength,
              row: j,
              length: matchlength,
              horizontal: true,
            });
          }

          matchlength = 1;
        }
      }
    }

    // Find vertical clusters
    for (var i = 0; i < 9; i++) {
      // Start with a single tile, cluster of 1
      var matchlength = 1;
      for (var j = 0; j < 9; j++) {
        var checkcluster = false;

        if (j == 9 - 1) {
          // Last tile
          checkcluster = true;
        } else {
          // Check the type of the next tile
          if (
            this.sequence[i][j] == this.sequence[i][j + 1] &&
            this.sequence[i][j] != ""
          ) {
            // Same type as the previous tile, increase matchlength
            matchlength += 1;
          } else {
            // Different type
            checkcluster = true;
          }
        }

        // Check if there was a cluster
        if (checkcluster) {
          if (matchlength >= 2) {
            // Found a vertical cluster
            clusters.push({
              column: i,
              row: j + 1 - matchlength,
              length: matchlength,
              horizontal: false,
            });
          }

          matchlength = 1;
        }
      }
    }
    return clusters;
  }
}



